import Page from 'flarum/common/components/Page';
import IndexPage from 'flarum/forum/components/IndexPage';
import Link from 'flarum/common/components/Link';
import LoadingIndicator from 'flarum/common/components/LoadingIndicator';
import listItems from 'flarum/common/helpers/listItems';
import ItemList from 'flarum/common/utils/ItemList';
import humanTime from 'flarum/common/helpers/humanTime';
import textContrastClass from 'flarum/common/helpers/textContrastClass';
import classList from 'flarum/common/utils/classList';
import tagIcon from '../../common/helpers/tagIcon';
import tagLabel from '../../common/helpers/tagLabel';
import sortTags from '../../common/utils/sortTags';
export default class TagsPage extends Page {
  oninit(vnode) {
    super.oninit(vnode);
    app.history.push('tags', app.translator.trans('flarum-tags.forum.header.back_to_tags_tooltip'));
    this.tags = [];
    const preloaded = app.preloadedApiDocument();
    if (preloaded) {
      this.tags = sortTags(preloaded.filter(tag => !tag.isChild()));
      return;
    }
    this.loading = true;
    app.tagList.load(['children', 'lastPostedDiscussion', 'parent']).then(() => {
      this.tags = sortTags(app.store.all('tags').filter(tag => !tag.isChild()));
      this.loading = false;
      m.redraw();
    });
  }
  oncreate(vnode) {
    super.oncreate(vnode);
    app.setTitle(app.translator.trans('flarum-tags.forum.all_tags.meta_title_text'));
    app.setTitleCount(0);
  }
  view() {
    return m("div", {
      className: "TagsPage"
    }, this.pageContent().toArray());
  }
  pageContent() {
    const items = new ItemList();
    items.add('hero', this.hero(), 100);
    items.add('main', m("div", {
      className: "container"
    }, this.mainContent().toArray()), 10);
    return items;
  }
  mainContent() {
    const items = new ItemList();
    items.add('sidebar', this.sidebar(), 100);
    items.add('content', this.content(), 10);
    return items;
  }
  content() {
    return m("div", {
      className: "TagsPage-content sideNavOffset"
    }, this.contentItems().toArray());
  }
  contentItems() {
    const items = new ItemList();
    if (this.loading) {
      items.add('loading', m(LoadingIndicator, null));
    } else {
      const pinned = this.tags.filter(tag => tag.position() !== null);
      const cloud = this.tags.filter(tag => tag.position() === null);
      items.add('tagTiles', this.tagTileListView(pinned), 100);
      if (cloud.length) {
        items.add('cloud', this.cloudView(cloud), 10);
      }
    }
    return items;
  }
  hero() {
    return IndexPage.prototype.hero();
  }
  sidebar() {
    return m("nav", {
      className: "TagsPage-nav IndexPage-nav sideNav"
    }, m("ul", null, listItems(this.sidebarItems().toArray())));
  }
  sidebarItems() {
    return IndexPage.prototype.sidebarItems();
  }
  tagTileListView(pinned) {
    return m("ul", {
      className: "TagTiles"
    }, pinned.map(this.tagTileView.bind(this)));
  }
  tagTileView(tag) {
    const lastPostedDiscussion = tag.lastPostedDiscussion();
    const children = sortTags(tag.children() || []);
    return m("li", {
      className: classList('TagTile', {
        colored: tag.color()
      }, textContrastClass(tag.color())),
      style: {
        '--tag-bg': tag.color()
      }
    }, m(Link, {
      className: "TagTile-info",
      href: app.route.tag(tag)
    }, tag.icon() && tagIcon(tag, {}, {
      useColor: false
    }), m("h3", {
      className: "TagTile-name"
    }, tag.name()), m("p", {
      className: "TagTile-description"
    }, tag.description()), !!children && m("div", {
      className: "TagTile-children"
    }, children.map(child => [m(Link, {
      href: app.route.tag(child)
    }, child.name()), ' ']))), lastPostedDiscussion ? m(Link, {
      className: "TagTile-lastPostedDiscussion",
      href: app.route.discussion(lastPostedDiscussion, lastPostedDiscussion.lastPostNumber())
    }, m("span", {
      className: "TagTile-lastPostedDiscussion-title"
    }, lastPostedDiscussion.title()), humanTime(lastPostedDiscussion.lastPostedAt())) : m("span", {
      className: "TagTile-lastPostedDiscussion"
    }));
  }
  cloudView(cloud) {
    return m("div", {
      className: "TagCloud"
    }, cloud.map(tag => [tagLabel(tag, {
      link: true
    }), ' ']));
  }
}
flarum.reg.add('flarum-tags', 'forum/components/TagsPage', TagsPage);